import React from "react";
import { Button, Form } from "react-bootstrap"
import { connect } from "react-redux"
import { HiOutlineRefresh } from "react-icons/hi"

import { UPDATE_CURRENCY_TYPE_ACTION } from "../../redux/actions/actions"

class HeaderComponent extends React.Component {
    state = {
        currency_type: this.props.currency_type
    }

    static getDerivedStateFromProps = async (props, state) => {
        state.currency_type = props.currency_type
        return state
    }

    handleCurrencyTypeChange = async (currency_type) => {

        this.props.UPDATE_CURRENCY_TYPE_ACTION(currency_type)
    }
    render() {
        const currency_types = ["Million", "Thousand"]

        return (
            <div className="header">
                <h5>{this.props.header_title}</h5>
                <div className="header__left">
                    <div>
                        <Button variant="secondary" size="sm" style={{ marginRight: 10 }} onClick={() => window.location.reload()}>
                            <HiOutlineRefresh color="green" />
                        </Button>
                    </div>
                    <Form.Group className="header__currency" controlId="exampleForm.ControlInput1">
                        {/* <Form.Label>Product</Form.Label> */}
                        <Form.Select aria-label="Default select example" size='sm' onChange={(e) => this.handleCurrencyTypeChange(e.target.value)}>
                            <option>{this.state.currency_type}</option>
                            {currency_types.map((currency_type, idx) =>
                                this.state.currency_type === currency_type ? null : <option key={idx} value={currency_type}>{currency_type}</option>
                            )}
                        </Form.Select>
                    </Form.Group>

                    <Button variant="secondary" size="sm">{this.props.first_name[0]}{this.props.last_name[0]}</Button>
                    <p className="header__header-name">{this.props.first_name} {this.props.last_name}</p>
                </div>
            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        currency_type: state.update_reducer.currency_type,
    }
}

const mapDispatchToProps = {
    UPDATE_CURRENCY_TYPE_ACTION
}

export default connect(
    mapStateToProps, mapDispatchToProps
)(HeaderComponent);