import React, { lazy, Suspense } from "react";
import { connect } from "react-redux";
import { Navigate } from "react-router-dom";

// import InputComponent from "./input_section"
import Layout from "../utils/layout";
// import ChartSection from "./chart_section";
// import TopCustomers from "./customer";

import {

    GET_TOP_CUSTOMER_ACTION,

    GET_DEPOT_MONTH_ACTION,

    GET_PRODUCT_SERIES_ACTION, GET_PRODUCT_MONTH_ACTION,

} from "../../redux/actions/actions";
import { addMonths } from "../utils/_funcs";
import SpinnerComponent from "../utils/spinner";

const InputComponent = lazy(() => import('./input_section'));
const ChartSection = lazy(() => import('./chart_section'));
const TopCustomers = lazy(() => import('./customer'));


class HomePage extends React.Component {
    state = {
        redirect: false,
        currency_type: this.props.currency_type,
        spinner: false,
    }

    static getDerivedStateFromProps(props, state) {
        const { token, top_customer, depot_month, depot_product_month, product_series, product_month } = props

        if (token === undefined || token === null) {
            state.redirect = true
            return state
        }
        if (top_customer !== undefined && depot_month !== undefined && product_month !== undefined && product_series !== undefined) {
            state.spinner = false
            return state
        }
        else {
            state.spinner = true
            return state
        }

        return null
    }

    componentDidMount = async () => {
        this.setState({ spinner: true })
        let end_date = this.props.selection_dates.endDate
        let start_date = this.props.selection_dates.startDate

        await this.props.GET_PRODUCT_SERIES_ACTION(start_date, end_date)
        await this.props.GET_PRODUCT_MONTH_ACTION(this.props.product_month_year)
        await this.props.GET_DEPOT_MONTH_ACTION(this.props.depot_month_year)
        if (this.props.top_customer_month === 13) {
            await this.props.GET_TOP_CUSTOMER_ACTION(this.props.top_customer_year, "False")
        } else {
            await this.props.GET_TOP_CUSTOMER_ACTION(this.props.top_customer_year, "True")
        }

    }


    render() {
        const { redirect } = this.state
        if (redirect === true) {
            return <Navigate to="/login" />
        }
        if (this.props.token.is_staff === false) {
            return <Navigate to="/depots" />
        }
        return (
            <Layout header_title={"Dashboard"} >
                <Suspense fallback={<SpinnerComponent spinner={true} />}>
                    <InputComponent />
                </Suspense>

                {this.state.spinner ? <SpinnerComponent spinner={this.state.spinner} /> :
                    <div>
                        <Suspense fallback={<SpinnerComponent spinner={true} />}>
                            <ChartSection
                                product_series={this.props.product_series}
                                product_month={this.props.product_month}
                                depot_month={this.props.depot_month}

                                selection_dates={this.props.selection_dates}
                                dashboard_metric={this.props.dashboard_metric}
                                currency_type={this.props.currency_type}
                                product_month_year={this.props.product_month_year}
                                depot_month_month={this.props.depot_month_month}
                                depot_month_year={this.props.depot_month_year}
                                update_product_series={this.props.update_product_series}
                                update_product_month={this.props.update_product_month}
                                update_depot_month={this.props.update_depot_month}

                            />
                        </Suspense>
                        <Suspense fallback={<SpinnerComponent spinner={true} />}>
                            <TopCustomers
                                dashboard_metric={this.props.dashboard_metric}
                                top_customer={this.props.top_customer}
                                top_customer_month={this.props.top_customer_month}
                                top_customer_year={this.props.top_customer_year}
                                product_top_customer={this.props.product_top_customer}
                                top_customer_product={this.props.top_customer_product}
                                update_top_customer={this.props.update_top_customer}
                            />
                        </Suspense>
                    </div>}
            </Layout>
        )
    }
}

const mapStateToProps = state => {
    return {
        token: state.auth_reducer.token,

        top_customer: state.list_reducer.top_customer,
        update_top_customer: state.list_reducer.update_top_customer,

        depot_month: state.list_reducer.depot_month,
        update_depot_month: state.list_reducer.update_depot_month,
        depot_product_month: state.list_reducer.depot_product_month,

        product_series: state.list_reducer.product_series,
        update_product_series: state.list_reducer.update_product_series,
        product_month: state.list_reducer.product_month,
        update_product_month: state.list_reducer.update_product_month,
        product_top_customer: state.list_reducer.product_top_customer,

        dashboard_metric: state.update_reducer.dashboard_metric,
        selection_dates: state.update_reducer.selection_dates,
        currency_type: state.update_reducer.currency_type,
        product_month_year: state.update_reducer.product_month_year,
        depot_month_month: state.update_reducer.depot_month_month,
        depot_month_year: state.update_reducer.depot_month_year,
        top_customer_month: state.update_reducer.top_customer_month,
        top_customer_year: state.update_reducer.top_customer_year,
        top_customer_product: state.update_reducer.top_customer_product,
    }
}

const mapDispatchToProps = {
    GET_TOP_CUSTOMER_ACTION,

    GET_DEPOT_MONTH_ACTION,

    GET_PRODUCT_SERIES_ACTION, GET_PRODUCT_MONTH_ACTION,
}

export default connect(
    mapStateToProps, mapDispatchToProps
)(HomePage);