import React from "react";
import { Container, Button } from 'react-bootstrap'
import { connect } from "react-redux";

import { MdLogout, MdDashboard, MdOutlineListAlt, MdPeople, MdAnchor } from "react-icons/md";
import { Link, Navigate, NavLink } from "react-router-dom";
import HeaderComponent from "./header";
import { URL } from "../../utils/_consts";
import { USER_LOGGED_OUT_ACTION } from "../../redux/actions/actions"

class Layout extends React.Component {
    state = {
        redirect: false,
    }

    handleLogOut = () => {
        this.props.USER_LOGGED_OUT_ACTION()
        this.setState({ redirect: true })

    }
    render() {

        if (this.state.redirect) {
            return <Navigate to={"/login"} />
        }
        return (
            <Container fluid className="layout_main">

                <div className="layout_main__side">
                    <Link to="/" className="layout_main__logo">
                        <img src={`${URL}/static/opl_logo.png`} alt="logo" className="layout_main__logo--img" />
                    </Link>

                    <div className="layout_main__links">
                        {this.props.token.is_staff === true ?
                            <NavLink to="/" className="layout_main__button">
                                <h6><span className="layout_main__links--span"><MdDashboard size={20} color="#D04D30" /></span>Dashboard</h6>
                            </NavLink> : null}

                        <NavLink to="/depots" className="layout_main__button">
                            <h6><span className="layout_main__links--span"><MdAnchor /></span>Depots</h6>
                        </NavLink>


                        <NavLink to="/sale" className="layout_main__button">
                            <h6><span className="layout_main__links--span"><MdOutlineListAlt /></span>Sales</h6>
                        </NavLink>

                        {this.props.token.is_staff === true ?
                            <NavLink to="/customers" className="layout_main__button">
                                <h6><span className="layout_main__links--span"><MdPeople /></span>Customers</h6>
                            </NavLink> : null}
                        <Button variant="transparent" className="layout_main__button layout_main__logout" onClick={this.handleLogOut}>
                            <h6><span className="layout_main__links--span"><MdLogout /></span>Log Out</h6>
                        </Button>
                    </div>
                </div>
                <div className="layout_main__component">
                    <HeaderComponent header_title={this.props.header_title} first_name={this.props.token.data.first_name} last_name={this.props.token.data.last_name} />
                    {this.props.children}
                </div>

            </Container>
        )
    }
}
const mapStateToProps = state => {
    return {
        token: state.auth_reducer.token,
    };
};

const mapDispatchToProps = {
    USER_LOGGED_OUT_ACTION,
};

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Layout);
